import { generateFilterFunction, ItemTableFilterComp } from "Util/Components/NewItemTable/Item/Filter/StandardItemFilter";
export const MerchantInventoryItemFilter = {
    defaultState: {},
    FilterComponent: WrapComponent,
    generateFilter: generateMerchantFilter
};
function WrapComponent({ items, state, setState }) {
    let sid = items.map(x => x.type === "itemdata" ? x.item : x.item._source);
    return <ItemTableFilterComp items={sid} state={state} setState={setState}/>;
}
function generateMerchantFilter(filter) {
    let itemFilter = generateFilterFunction(filter);
    return (item) => itemFilter(item.item);
}
