export function chainSort(...comparators) {
    return (a, b) => {
        for (let c of comparators) {
            let r = c(a, b);
            if (r != 0)
                return r;
        }
        return 0;
    };
}
export function mapSort(map, comparator) {
    return (a, b) => comparator(map(a), map(b));
}
export const StringSorter = (a, b) => a.localeCompare(b);
export const NumSorter = (a, b) => a - b;
