import { CircularProgress } from "@mui/material";
import { Suspense } from "react";
export function SuspenseLoadIndicator() {
    return <div>
        <CircularProgress />
    </div>;
}
export function SuspenseLayer({ children }) {
    return <Suspense fallback={<SuspenseLoadIndicator />}>
        {children}
    </Suspense>;
}
