import { Tooltip as MUITooltip } from "@mui/material";
export function Tooltip({ text, children, wrapSpan }) {
    if (!children)
        return null;
    if (text) {
        let c = wrapSpan ? <span>{children}</span> : children;
        return <MUITooltip title={text}>
            {c}
        </MUITooltip>;
    }
    else {
        return children;
    }
}
