import { createContext, useContext, useEffect, useRef } from "react";
import { SuspenseCache } from "Util/Suspense/SuspenseCache";
import { useRefresh } from "Util/Helper/useForceUpdate";
const Ctx = createContext(null);
export function SuspenseContext({ children }) {
    let refresh = useRefresh();
    let ref = useRef(new SuspenseCache(refresh));
    return <Ctx.Provider value={ref.current}>
        {children}
    </Ctx.Provider>;
}
export function useSuspensePromise(key, generator, deps = []) {
    let ctx = useContext(Ctx);
    let result = ctx.get(key, generator);
    let { id } = result;
    useEffect(() => {
        ctx.watch(key, id);
        return () => ctx.unwatch(key, id);
    }, [...deps, id]);
    if (result.loading)
        throw result.promise;
    if (result.error)
        throw result.error.value;
    return result.result;
}
export function useClearSuspenseCache(key) {
    let ctx = useContext(Ctx);
    return () => ctx.clear(key);
}
