let promise;
async function loadRemoteSpellData() {
    try {
        let response = await fetch("https://5e.tools/data/generated/gendata-spell-source-lookup.json");
        if (response.status !== 200)
            throw Error("Cry");
        let f = await response.json();
        return f;
    }
    catch (e) {
        console.error("Error fetching spell data", e);
        return null;
    }
}
export async function getRemoteSpellData() {
    if (promise)
        return promise;
    promise = loadRemoteSpellData();
    return promise;
}
