export class SuspenseCache {
    refresh;
    results = {};
    index = 0;
    constructor(refresh) {
        this.refresh = refresh;
    }
    get(key, generator) {
        if (this.results[key])
            return this.results[key];
        console.log(`Suspense generating promise [${key}]`);
        let promise = generator();
        let value = {
            loading: true,
            promise: promise,
            result: null,
            count: 0,
            id: this.index++
        };
        promise.then(r => {
            value.loading = false;
            value.promise = undefined;
            value.result = r;
        }).catch(r => {
            value.loading = false;
            value.error = { value: r };
            value.promise = undefined;
        });
        this.results[key] = value;
        return value;
    }
    getWithId(key, id) {
        let r = this.results[key];
        if (r) {
            if (r.id !== id) {
                console.log("Id mismatch");
                return undefined;
            }
            return r;
        }
        return undefined;
    }
    watch(key, id) {
        let r = this.getWithId(key, id);
        if (r) {
            r.count++;
            console.log(`Suspense watched [${key}] [${r.count}]`);
        }
        else {
            console.log(`Suspense failed to watch [${key}]`);
        }
    }
    unwatch(key, id) {
        let r = this.getWithId(key, id);
        if (r) {
            r.count--;
            console.log(`Suspense uwatched [${key}] [${r.count}]`);
            if (r.count <= 0) {
                this.clear(key);
            }
        }
    }
    clear(key) {
        if (this.results[key]) {
            this.results[key].id = -1;
        }
        delete this.results[key];
        this.refresh();
    }
}
