import { ReactObj } from "Util/React/ReactMixin";
export class ReactActorSheet extends ActorSheet {
    reactObj = new ReactObj(this);
    static get defaultOptions() {
        return mergeObject(super.defaultOptions, {
            template: "modules/MorrowinDnDReact/templates/ReactApplication.hbs",
            width: (super.defaultOptions.width ?? 0) + 120
        });
    }
    getComponent() {
        return <div>Test</div>;
    }
}
export class ReactItemSheet extends ItemSheet {
    reactObj = new ReactObj(this);
    static get defaultOptions() {
        return mergeObject(super.defaultOptions, {
            template: "modules/MorrowinDnDReact/templates/ReactApplication.hbs",
            width: (super.defaultOptions.width ?? 0) + 120,
            height: 700
        });
    }
    getComponent() {
        return <div />;
    }
}
